	module.exports = {"data":{"markdownRemark":{"html":"<p>Yesterday a couple friends and I went on a bike ride that took us across the Golden Gate Bridge, to the top of <a href=\"https://en.wikipedia.org/wiki/Mount_Tamalpais\">a mountain</a> where it was unpleasantly cold and windy, then down to a beach where we passed out in the sun for a couple hours before hopping back on the bike to ride along the coast back to the city. The day before that I was visiting a <a href=\"https://www.noisebridge.net/wiki/Noisebridge\">a “doocratic” (i.e. anarchic) hacker space</a> where I learned about how such a place functions as well as video game development. It’s such contrasting experiences like these, in such a short time frame, that make me feel that the 62% higher rent and everything else I put up with to live here is somewhat justified. This post is super short and more incomplete than usual because the juices just don’t seem to be flowing today. Perhaps I’ll expand on it another day. For now, here’s some pictures from the ride:</p>\n<p><img src=\"/2018-7-4-strava-da43691c521edf0a96ec0629785a5c13.png\" alt=\"The Stravas\">\nOur route according to Strava</p>\n<p><img src=\"/2018-7-4-faces-8813948371b575803c4690ed81576977.jpg\" alt=\"The end is near?\">\nAre we there yet??</p>","excerpt":"Yesterday a couple friends and I went on a bike ride that took us across the Golden Gate Bridge, to the top of  a mountain  where it was unpleasantly cold and windy, then down to a beach where we passed out in the sun for a couple hours before…","frontmatter":{"date":"July 05, 2018","path":"/blog/2018/7/5","title":"Contrasting experiences","category":"life, the universe and everything"}}},"pathContext":{}}

